import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Checkbox } from "primereact/checkbox";
import { InputText } from "primereact/inputtext";
import { ListBox } from "primereact/listbox";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { roleActions } from "../../store/role/roleSlice";
import { userActions } from "../../store/user/userSlice";
import { IAppState } from "../../models/appState";
import { IRole } from "../../models/role";
import { ILabelValue } from "../../models/labelValue";

const RoleEdit: React.FC = () => {
  const permissionsData = [
    "Account.Edit",
    "Accounts.View",
    "Campaign.Edit",
    "Campaigns.View",
    "Contact.Edit",
    "Contacts.View",
    "Currency.Edit",
    "Currencies.View",
    "GLAccount.Edit",
    "GLAccounts.View",
    "Invoice.Edit",
    "Invoices.View",
    "Lead.Edit",
    "Leads.View",
    "Manufacturer.Edit",
    "Manufacturers.View",
    "Opportunity.Edit",
    "Opportunities.View",
    "Product.Edit",
    "Products.View",
    "ProductCategory.Edit",
    "ProductCategories.View",
    "PurchaseOrder.Edit",
    "PurchaseOrders.View",
    "Role.Edit",
    "Roles.View",
    "SalesOrder.Edit",
    "SalesOrders.View",
    "Tax.Edit",
    "Taxes.View",
    "UsageUnit.Edit",
    "UsageUnits.View",
    "User.Edit",
    "Users.View",
    "Vendor.Edit",
    "Vendors.View",
  ];
  const defaultItem: IRole = {
    id: "",
    name: "",
    permissions: [],
  };
  const navigateTo = "/roles";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [usersData, setUsersData] = useState<ILabelValue[]>([
    { label: "", value: "" },
  ]);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);
  const [role, isFetching, isError, roles, users] = useAppSelector(
    (state: IAppState) => [
      state.roles.role,
      state.roles.isFetching,
      state.roles.isError,
      state.roles.roles,
      state.users.users,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(roleActions.getRoleById(id));
          dispatch(userActions.getUsers());
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== role?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(role);

      if (role.name && users && users.length > 0) {
        const roleName = role.name || "";
        const data: ILabelValue[] = [];
        users.forEach((value) => {
          if (value.roles && value.roles.indexOf(roleName) > -1) {
            data.push({ label: value.email, value: value.email });
          }
        });

        if (data.length > 0) {
          setUsersData(data);
        }
      }
    }
  }, [params, item, role, isFetching, isError, users]);

  const isNameValid = useCallback(
    (name: string) => {
      let result = true;
      if (item.id.length === 0) {
        if (roles.find((x) => x.name?.toLowerCase() === name?.toLowerCase())) {
          result = false;
        }
      } else {
        if (
          roles.find(
            (x) =>
              x.name?.toLowerCase() === name?.toLowerCase() && x.id !== item.id
          )
        ) {
          result = false;
        }
      }

      return result;
    },
    [item.id, roles]
  );

  useEffect(() => {
    if (!dirty) {
      return;
    }

    let isValid = true;
    for (const [key, value] of Object.entries(item)) {
      if (key === "id") {
        continue;
      }

      if (typeof value === "string") {
        if (value?.trim().length === 0) {
          isValid = false;
          break;
        }
      }
    }

    if (isValid) {
      isValid = (item.permissions || []).length > 0;
    }

    setValid(isValid && isNameValid(item.name || ""));
  }, [dirty, item, isNameValid]);

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    setItem({ ...item, [tname]: tvalue });
    if (tname === "name") {
      const isValidName = isNameValid(tvalue);
      setErrors({
        ...errors,
        nameInvalid: !isValidName,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    } else {
      setErrors({
        ...errors,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    }
  };

  const onPermissionChange = (e: any) => {
    setDirty(true);
    const items = [...(item.permissions || [])];
    if (e.checked) {
      items.push(e.value);
    } else {
      items.splice(items.indexOf(e.value), 1);
    }
    setItem({ ...item, permissions: items });
  };

  const mapPermissions = () => {
    return permissionsData.map((value) => (
      <li key={value}>
        <Checkbox
          name="permission"
          inputId={value}
          value={value}
          onChange={onPermissionChange}
          checked={(item.permissions || []).some((data) => data === value)}
        />
        <label htmlFor={value} className="p-checkbox-label">
          {value}
        </label>
      </li>
    ));
  };

  const onSaveClick = async () => {
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Role: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Role</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="RoleName">
                  Role Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="RoleName"
                  name="name"
                  maxLength={36}
                  placeholder="Administrator"
                  value={item.name}
                  onChange={onChange}
                />
                {(errors as any)?.name && (
                  <Message severity="error" text="Role Name is required." />
                )}
                {(errors as any)?.nameInvalid && (
                  <Message severity="error" text="Role Name already exists." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label>Assigned to Users</label>
              </div>
              <div className="col">
                <ListBox
                  options={usersData}
                  style={{ width: "15rem" }}
                  listStyle={{ maxHeight: "250px" }}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Permissions</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <ul style={{ listStyleType: "none" }}>{mapPermissions()}</ul>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default RoleEdit;
